@use './config/tailwind'; //garder en haut compl√®tement pour pouvoir overwrite tous les styles de base dans mon compiler.
@use './theme/colors';
@use './theme/fonts';


.screen-reader-text {
    border: 0;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
    word-wrap: normal !important;
  }
  